.Home {
  background: -webkit-gradient(linear, left top, left bottom, from(#50C1E9), to(#3BAEDA));
  background: -webkit-linear-gradient(#50C1E9, #3BAEDA);
  background: linear-gradient(#50C1E9, #3BAEDA);
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
/*# sourceMappingURL=home-layout.css.map */
.logo {
  font-size: 3em;
  color: #434A54;
}
/*# sourceMappingURL=logo.css.map */
.login {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  color: #434A54;
}

.login form {
  padding: 20px 0;
  display: flex;
  flex-flow: column nowrap;
}

.login form .input {
  width: 250px;
  margin: 5px 0;
  border-radius: 20px;
  background-color: white;
  padding: 5px 15px;
  border: 2px solid #434A54;
}

.login form .input input {
  font-size: 1.3em;
  background: none;
  border: none;
  outline: none;
  color: #434A54;
}

.login form .input input::-webkit-input-placeholder {
  color: #CCD0D9;
}

.login form .input input::-ms-input-placeholder {
  color: #CCD0D9;
}

.login form .input input::placeholder {
  color: #CCD0D9;
}
/*# sourceMappingURL=login.css.map */
button {
  margin-top: 10px;
  border-radius: 20px;
  outline: none;
  border: none;
  padding: 10px 0;
  font-size: 1.3em;
  background: -webkit-gradient(linear, left top, left bottom, from(#48CFAE), to(#48CFAE));
  background: -webkit-linear-gradient(#48CFAE, #48CFAE);
  background: linear-gradient(#48CFAE, #48CFAE);
  color: white;
  cursor: pointer;
  -webkit-transition: background .2s, -webkit-transform .1s;
  transition: background .2s, -webkit-transform .1s;
  transition: transform .1s, background .2s;
  transition: transform .1s, background .2s, -webkit-transform .1s;
  box-shadow: inset -3px -3px 0px #29826c;
  width: 100%;
}

button:hover {
  background: #434A54;
  box-shadow: inset -3px -3px 0px #2d3138;
}

button:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.exit {
  background: #F25757;
  box-shadow: inset -3px -3px 0px #af3535;
}
/*# sourceMappingURL=button.css.map */
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 2em;
  background-color: #434A54;
  text-decoration: none;
  width: 100%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer h2 {
  color: #AAB2BD;
  font-size: 1.2em;
  text-transform: uppercase;
  -webkit-transition: .3s;
  transition: .3s;
}

.footer h2:hover {
  color: white;
}

@media screen and (min-width: 768px) {
  .footer {
    font-size: 1em;
  }
}
/*# sourceMappingURL=footer.css.map */
.panel {
  background: -webkit-gradient(linear, left top, left bottom, from(#50C1E9), to(#3BAEDA));
  background: -webkit-linear-gradient(#50C1E9, #3BAEDA);
  background: linear-gradient(#50C1E9, #3BAEDA);
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.panel ul {
  width: 55%;
  padding: 0;
}

@media screen and (min-width: 768px) {
  .panel ul {
    width: 35%;
  }
}

.panel ul li {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  list-style: none;
  margin: 5px 0;
}
/*# sourceMappingURL=panel.css.map */
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,h2,h3,h4,h5,h6{
  margin: 0;
}

*{
  box-sizing: border-box;
}

*,
*::after,
*::before,{
  box-sizing: inherit;
}

.app{
  width: 100vw;
  height: 100vh;
  position: relative;
}
a{
  width: inherit;
}
